<template>
    <a-form @submit="handleSubmit"
            :autoFormCreate="(form)=>{this.form = form}">

        <a-card title="系统信息" :bordered="false" style="width:100%">
            <a-form-item
                    label='所属党支部'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    fieldDecoratorId="department_id"
            >
                <SelectDepartment/>
            </a-form-item>
            <a-form-item
                    label='角色'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    fieldDecoratorId="role_ids"
            >
                <SelectRole/>
            </a-form-item>
            <a-form-item
                    label='账户类型'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    :fieldDecoratorOptions="{valuePropName:'checked'}"
                    fieldDecoratorId="type"
            >
                <a-select>
                    <a-select-option :value="2">领导</a-select-option>
                    <a-select-option :value="3">党员</a-select-option>
                    <!--<a-select-option value="4">群众</a-select-option>-->
                </a-select>
            </a-form-item>
        </a-card>

        <a-card title="基本信息" :bordered="false" style="width:100%">
            <a-form-item
                    label='姓名'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    fieldDecoratorId="user_name"
            >
                <a-input/>
            </a-form-item>
            <a-form-item
                    label='简介'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    fieldDecoratorId="user_excerpt"
            >
                <a-textarea :autosize="{ minRows: 2, maxRows: 6 }"/>
            </a-form-item>
            <a-form-item
                    label='性别'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    :fieldDecoratorOptions="{valuePropName:'checked'}"
                    fieldDecoratorId="user_sex"
            >
                <a-radio-group buttonStyle="solid">
                    <a-radio-button value="男">男</a-radio-button>
                    <a-radio-button value="女">女</a-radio-button>
                </a-radio-group>
            </a-form-item>
            <a-form-item
                    label='职务'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    fieldDecoratorId="user_duty"
            >
                <a-input/>
            </a-form-item>
            <a-form-item
                    label='照片'
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 18 }"
                    fieldDecoratorId="more_thumbnail"
            >
                <upload-thumbnail/>
            </a-form-item>
        </a-card>

        <template v-if="!id">
            <a-card title="账户信息" :bordered="false" style="width:100%">
                <a-form-item
                        label='登录账户'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="user_login"
                >
                    <a-input :disabled="!!this.id">
                        <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                </a-form-item>
                <a-form-item
                        label='密码'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="user_password"
                >
                    <a-input type='password'>
                        <a-icon slot="prefix" type='lock' style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                </a-form-item>
                <a-form-item
                        label='再次输入密码'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 18 }"
                        fieldDecoratorId="user_password2"
                >
                    <a-input type='password'>
                        <a-icon slot="prefix" type='lock' style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                </a-form-item>
            </a-card>

        </template>

        <a-form-item
                :wrapperCol="{ span: 12, offset: 5 }"
        >
            <a-button type='primary' htmlType='submit'>
                提交
            </a-button>
        </a-form-item>
    </a-form>
</template>

<script>
    import {getUser, createUser, updateUser} from '@/api/user'
    import uploadThumbnail from '@/components/upload/UploadThumbnail'
    import uploadImages from '@/components/upload/UploadImages'
    import uploadFiles from '@/components/upload/UploadFiles'
    import selectCategory from '@/components/select/SelectCategory'
    import SelectDepartment from '@/components/select/SelectDepartment'
    import SelectRole from '@/components/select/SelectRole'

    export default {
        components: {
            SelectDepartment, selectCategory, SelectRole, uploadThumbnail, uploadImages, uploadFiles
        },
        props: {
            id: {
                type: Number,
                default: 0,
            }
        },
        data() {
            return {
                log: console.log,
                form: null,
            }
        },
        methods: {
            init() {
                if (!this.id) {
                    return;
                }

                getUser(this.id)
                    .then(res => {
                        let thumbnailUrl = (res.data.more && res.data.more.hasOwnProperty('thumbnail'))
                            ? res.data.more.thumbnail : '';

                        this.form.setFieldsValue({
                            type: res.data.type,
                            role_ids: res.data.role_ids,
                            department_id: res.data.department_id,
                            name: res.data.name,
                            sex: res.data.sex,
                            duty: res.data.duty,
                            user_excerpt: res.data.user_excerpt,
                            // user_login: res.data.user_login,
                            more_thumbnail: thumbnailUrl,
                        })
                    })
            },
            beforeSubmit(e) {
                e.preventDefault();

                return true;
                let res = false
                // this.form.validateFields(
                //     (err) => {
                //         if (!err) {
                //             res = true
                //         }
                //     })
                return res
            },
            handleSubmit() {
                let request;

                let data = this.form.getFieldsValue();

                if (this.id) {
                    request = updateUser(this.id, data)
                } else {
                    request = createUser(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                })
            },
        },
        mounted() {
            this.init();
        }
    }
</script>
