<template>
    <a-checkbox-group :options="options" :value="value" @change="onChange"/>
</template>

<script>
    import {TreeSelect} from 'ant-design-vue'
    import {getAuthRoleList} from '@/api/authRole'

    export default {
        props: {
            value: {
                required: false,
                default() {
                    return []
                }
            },
            styleValue: {
                required: false,
                default: () => {
                    return {'min-width': '200px'}
                }
            },
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data() {
            return {
                options: [],
            }
        },
        methods: {
            init() {
                getAuthRoleList({current_page: 0, filter: {}})
                    .then(res => {
                        let rows = res.data.rows;
                        let result = [];
                        for (let i = 0; i < rows.length; i++) {
                            result.push({
                                label: rows[i].name, value: rows[i].id
                            });
                        }
                        this.options = result;
                        // this.treeData = res;
                    })
            },
            onChange(value) {
                this.$emit('change', value);
            },
        },
        watch: {},
        created() {
            this.init();
        }
    }
</script>
